'use client'

import { useState } from 'react'

// Business Profile form data for A2P verification
interface BusinessProfile {
  // Basic Info
  businessName: string
  legalBusinessName: string
  businessType: string
  businessRegistrationNumber: string
  taxId: string
  
  // Contact Info
  businessEmail: string
  businessPhone: string
  websiteUrl: string
  
  // Address
  streetAddress: string
  streetAddress2: string
  city: string
  state: string
  postalCode: string
  country: string
  
  // Additional Info
  industry: string
  employeeCount: string
  yearEstablished: string
  description: string
}

const businessTypes = [
  'Sole Proprietorship',
  'Partnership',
  'LLC',
  'Corporation',
  'Non-Profit',
  'Government',
  'Other'
]

const industries = [
  'Agriculture',
  'Automotive',
  'Banking & Finance',
  'Construction',
  'Education',
  'Entertainment',
  'Healthcare',
  'Hospitality',
  'Insurance',
  'Legal Services',
  'Manufacturing',
  'Marketing & Advertising',
  'Real Estate',
  'Retail',
  'Technology',
  'Telecommunications',
  'Transportation',
  'Other'
]

const employeeCounts = [
  '1-10',
  '11-50',
  '51-200',
  '201-500',
  '501-1000',
  '1000+'
]

export default function BusinessProfilePage() {
  const [profile, setProfile] = useState<BusinessProfile>({
    businessName: '',
    legalBusinessName: '',
    businessType: '',
    businessRegistrationNumber: '',
    taxId: '',
    businessEmail: '',
    businessPhone: '',
    websiteUrl: '',
    streetAddress: '',
    streetAddress2: '',
    city: '',
    state: '',
    postalCode: '',
    country: 'United States',
    industry: '',
    employeeCount: '',
    yearEstablished: '',
    description: ''
  })
  
  const [isSaving, setIsSaving] = useState(false)
  const [saveSuccess, setSaveSuccess] = useState(false)
  const [errors, setErrors] = useState<Partial<Record<keyof BusinessProfile, string>>>({})

  const handleChange = (field: keyof BusinessProfile, value: string) => {
    setProfile(prev => ({ ...prev, [field]: value }))
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }))
    }
  }

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof BusinessProfile, string>> = {}
    
    if (!profile.businessName.trim()) newErrors.businessName = 'Business name is required'
    if (!profile.legalBusinessName.trim()) newErrors.legalBusinessName = 'Legal business name is required'
    if (!profile.businessType) newErrors.businessType = 'Business type is required'
    if (!profile.businessEmail.trim()) newErrors.businessEmail = 'Business email is required'
    if (!profile.businessPhone.trim()) newErrors.businessPhone = 'Business phone is required'
    if (!profile.streetAddress.trim()) newErrors.streetAddress = 'Street address is required'
    if (!profile.city.trim()) newErrors.city = 'City is required'
    if (!profile.state.trim()) newErrors.state = 'State is required'
    if (!profile.postalCode.trim()) newErrors.postalCode = 'Postal code is required'
    if (!profile.country) newErrors.country = 'Country is required'
    
    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return
    
    setIsSaving(true)
    setSaveSuccess(false)
    
    try {
      // TODO: API call to save business profile
      await new Promise(resolve => setTimeout(resolve, 1000)) // Simulate API call
      
      setSaveSuccess(true)
      setTimeout(() => setSaveSuccess(false), 3000)
    } catch (error) {
      console.error('Failed to save business profile:', error)
    } finally {
      setIsSaving(false)
    }
  }

  return (
    <div className="p-6 lg:p-8 max-w-4xl">
      {/* Header */}
      <div className="mb-8">
        <h2 className="text-2xl font-bold text-gray-900">Business Profile</h2>
        <p className="mt-1 text-gray-600">
          This information is used for A2P 10DLC verification and business identification.
        </p>
      </div>

      {/* A2P Info Banner */}
      <div className="mb-6 bg-amber-50 border border-amber-200 rounded-xl p-4">
        <div className="flex items-start gap-3">
          <i className="fas fa-exclamation-triangle text-amber-600 mt-0.5"></i>
          <div>
            <p className="text-sm font-medium text-amber-800">A2P 10DLC Verification Required</p>
            <p className="text-sm text-amber-700 mt-1">
              Complete this profile accurately to enable SMS messaging. Incorrect information may result in verification rejection.
            </p>
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Basic Business Information */}
        <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
          <div className="px-6 py-4 bg-gray-50 border-b border-gray-200">
            <h3 className="font-semibold text-gray-900">
              <i className="fas fa-building mr-2 text-gray-500"></i>
              Basic Information
            </h3>
          </div>
          <div className="p-6 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Business Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.businessName}
                  onChange={(e) => handleChange('businessName', e.target.value)}
                  placeholder="Your Company Name"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.businessName ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.businessName && (
                  <p className="mt-1 text-sm text-red-600">{errors.businessName}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Legal Business Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.legalBusinessName}
                  onChange={(e) => handleChange('legalBusinessName', e.target.value)}
                  placeholder="Registered legal name"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.legalBusinessName ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.legalBusinessName && (
                  <p className="mt-1 text-sm text-red-600">{errors.legalBusinessName}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Business Type <span className="text-red-500">*</span>
                </label>
                <select
                  value={profile.businessType}
                  onChange={(e) => handleChange('businessType', e.target.value)}
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-white ${
                    errors.businessType ? 'border-red-500' : 'border-gray-300'
                  }`}
                >
                  <option value="">Select business type</option>
                  {businessTypes.map(type => (
                    <option key={type} value={type}>{type}</option>
                  ))}
                </select>
                {errors.businessType && (
                  <p className="mt-1 text-sm text-red-600">{errors.businessType}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Industry
                </label>
                <select
                  value={profile.industry}
                  onChange={(e) => handleChange('industry', e.target.value)}
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-white"
                >
                  <option value="">Select industry</option>
                  {industries.map(industry => (
                    <option key={industry} value={industry}>{industry}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Business Registration Number
                </label>
                <input
                  type="text"
                  value={profile.businessRegistrationNumber}
                  onChange={(e) => handleChange('businessRegistrationNumber', e.target.value)}
                  placeholder="e.g., EIN, Company Number"
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Tax ID (EIN)
                </label>
                <input
                  type="text"
                  value={profile.taxId}
                  onChange={(e) => handleChange('taxId', e.target.value)}
                  placeholder="XX-XXXXXXX"
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Employee Count
                </label>
                <select
                  value={profile.employeeCount}
                  onChange={(e) => handleChange('employeeCount', e.target.value)}
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-white"
                >
                  <option value="">Select range</option>
                  {employeeCounts.map(count => (
                    <option key={count} value={count}>{count}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Year Established
                </label>
                <input
                  type="text"
                  value={profile.yearEstablished}
                  onChange={(e) => handleChange('yearEstablished', e.target.value)}
                  placeholder="e.g., 2010"
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Business Description
              </label>
              <textarea
                value={profile.description}
                onChange={(e) => handleChange('description', e.target.value)}
                rows={3}
                placeholder="Brief description of your business..."
                className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
              />
            </div>
          </div>
        </div>

        {/* Contact Information */}
        <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
          <div className="px-6 py-4 bg-gray-50 border-b border-gray-200">
            <h3 className="font-semibold text-gray-900">
              <i className="fas fa-address-card mr-2 text-gray-500"></i>
              Contact Information
            </h3>
          </div>
          <div className="p-6 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Business Email <span className="text-red-500">*</span>
                </label>
                <input
                  type="email"
                  value={profile.businessEmail}
                  onChange={(e) => handleChange('businessEmail', e.target.value)}
                  placeholder="contact@company.com"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.businessEmail ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.businessEmail && (
                  <p className="mt-1 text-sm text-red-600">{errors.businessEmail}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Business Phone <span className="text-red-500">*</span>
                </label>
                <input
                  type="tel"
                  value={profile.businessPhone}
                  onChange={(e) => handleChange('businessPhone', e.target.value)}
                  placeholder="+1 (555) 123-4567"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.businessPhone ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.businessPhone && (
                  <p className="mt-1 text-sm text-red-600">{errors.businessPhone}</p>
                )}
              </div>

              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Website URL
                </label>
                <input
                  type="url"
                  value={profile.websiteUrl}
                  onChange={(e) => handleChange('websiteUrl', e.target.value)}
                  placeholder="https://www.company.com"
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
                />
              </div>
            </div>
          </div>
        </div>

        {/* Business Address */}
        <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
          <div className="px-6 py-4 bg-gray-50 border-b border-gray-200">
            <h3 className="font-semibold text-gray-900">
              <i className="fas fa-map-marker-alt mr-2 text-gray-500"></i>
              Business Address
            </h3>
          </div>
          <div className="p-6 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Street Address <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.streetAddress}
                  onChange={(e) => handleChange('streetAddress', e.target.value)}
                  placeholder="123 Business Street"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.streetAddress ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.streetAddress && (
                  <p className="mt-1 text-sm text-red-600">{errors.streetAddress}</p>
                )}
              </div>

              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Street Address Line 2
                </label>
                <input
                  type="text"
                  value={profile.streetAddress2}
                  onChange={(e) => handleChange('streetAddress2', e.target.value)}
                  placeholder="Suite, Floor, Building (optional)"
                  className="w-full px-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  City <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.city}
                  onChange={(e) => handleChange('city', e.target.value)}
                  placeholder="City"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.city ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.city && (
                  <p className="mt-1 text-sm text-red-600">{errors.city}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  State/Province <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.state}
                  onChange={(e) => handleChange('state', e.target.value)}
                  placeholder="State"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.state ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.state && (
                  <p className="mt-1 text-sm text-red-600">{errors.state}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Postal Code <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={profile.postalCode}
                  onChange={(e) => handleChange('postalCode', e.target.value)}
                  placeholder="12345"
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 ${
                    errors.postalCode ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.postalCode && (
                  <p className="mt-1 text-sm text-red-600">{errors.postalCode}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Country <span className="text-red-500">*</span>
                </label>
                <select
                  value={profile.country}
                  onChange={(e) => handleChange('country', e.target.value)}
                  className={`w-full px-4 py-2.5 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-white ${
                    errors.country ? 'border-red-500' : 'border-gray-300'
                  }`}
                >
                  <option value="United States">United States</option>
                  <option value="Canada">Canada</option>
                  <option value="United Kingdom">United Kingdom</option>
                  <option value="Australia">Australia</option>
                  <option value="Other">Other</option>
                </select>
                {errors.country && (
                  <p className="mt-1 text-sm text-red-600">{errors.country}</p>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* Save Button */}
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            {saveSuccess && (
              <span className="text-green-600 flex items-center gap-2">
                <i className="fas fa-check-circle"></i>
                Saved successfully!
              </span>
            )}
          </div>
          <button
            type="submit"
            disabled={isSaving}
            className="px-6 py-2.5 bg-red-600 text-white font-medium rounded-lg hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            {isSaving ? (
              <>
                <i className="fas fa-spinner fa-spin"></i>
                Saving...
              </>
            ) : (
              <>
                <i className="fas fa-save"></i>
                Save Business Profile
              </>
            )}
          </button>
        </div>
      </form>
    </div>
  )
}
